<template>
  <div class="container">
    <div class="shirtPreview">
      <gradient-shirt
        :colorStops="colorStops"
        :direction="direction.toString()"
        :type="type"
      >
      </gradient-shirt>
    </div>
    <div class="customizer">
      <div class="field colors">
        <label for="colorPicker">Gradient Colors:</label>
        <div class="colorList">
          <span
            class="color"
            v-for="(color, index) in colorStops"
            :key="color"
            @click="removeColor(index)"
          >
            {{ color }}
          </span>
        </div>
        <div class="colorAdder">
          <input
            type="color"
            name="colorPicker"
            id="colorPicker"
            v-model="newColor"
          />
          <button class="addColor" @click="addNewColor">Add</button>
        </div>
      </div>
      <div class="field direction">
        <label for="degreePicker"> Rotation: {{ direction }}</label>
        <input
          type="range"
          name="degreePicker"
          id=""
          min="0"
          max="360"
          v-model="direction"
        />
      </div>
      <div class="field type">
        <label for="typeSelector">Gradient Type:</label>
        <select name="typeSelector" id="" v-model="type">
          <option value="linear">Linear</option>
          <option value="radial">Radial</option>
        </select>
      </div>
      <div class="field size">
        <label for="sizePicker">Pick a size:</label>
        <select name="sizePicker" id="sizePicker" v-model="shirtSize">
          <option value="XS">XS</option>
          <option value="S">S</option>
          <option value="M">M</option>
          <option value="L">L</option>
          <option value="XL">XL</option>
        </select>
      </div>
      <div class="field creatorName">
        <label for="creatorName">Creator:</label>
        <input
          type="text"
          name="creatorName"
          id="creatorName"
          v-model="creator"
        />
      </div>

      <button
        :disabled="!creator || colorStops.length <= 1"
        class="submit"
        @click="createShirt"
      >
        Create Shirt
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import GradientShirt from "@/components/GradientShirt.vue";

export default {
  name: "Shirt Creator",
  data() {
    return {
      colorStops: [],
      newColor: "",
      direction: 0,
      type: "linear",
      shirtSize: "M",
      creator: "",
    };
  },
  methods: {
    async createShirt() {
      const variables = {
        color_stops: this.colorStops,
        direction: this.direction.toString(),
        type: this.type,
        size: this.shirtSize,
        creator: this.creator,
      };
      console.log(variables);
      const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
      const query = `
      mutation createGradientShirt($color_stops: [String!]!, $direction: String!, $type: GradientType!, $size: ShirtSize!, $creator: String!) {
        shirt: createGradientShirt(color_stops: $color_stops, direction: $direction, type: $type, size: $size, creator: $creator) {
          id,
        },
      }
      `;
      const body = {
        query,
        variables,
      };
      const response = (await axios.post(endpoint, body)).data;
      console.log("Response:", response);
      const { id } = response.data.shirt;
      console.log("Created Shirt ID:", id);
      this.$router.push({ name: "gradient", params: { id } });
    },
    addNewColor() {
      if (!this.newColor) return;
      this.colorStops.push(this.newColor);
      this.newColor = "";
    },
    removeColor(index) {
      this.colorStops.splice(index, 1);
    },
  },
  components: {
    GradientShirt,
  },
};
</script>

