<template>
  <div class="container">
    <div class="shirtPreview">
      <shirt :color="shirtColor"></shirt>
    </div>
    <div class="customizer">
      <div class="field color">
        <label for="colorPicker">Pick a color:</label>
        <input
          type="color"
          name="colorPicker"
          id="colorPicker"
          v-model="shirtColor"
        />
      </div>
      <div class="field size">
        <label for="sizePicker">Pick a size:</label>
        <select name="sizePicker" id="sizePicker" v-model="shirtSize">
          <option value="XS">XS</option>
          <option value="S">S</option>
          <option value="M">M</option>
          <option value="L">L</option>
          <option value="XL">XL</option>
        </select>
      </div>
      <div class="field creatorName">
        <label for="creatorName">Creator:</label>
        <input
          type="text"
          name="creatorName"
          id="creatorName"
          v-model="creator"
        />
      </div>

      <button :disabled="!creator" class="submit" @click="createShirt">
        Create Shirt
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Shirt from "@/components/Shirt.vue";

export default {
  name: "Shirt Creator",
  data() {
    return {
      shirtColor: "gray",
      shirtSize: "M",
      creator: "",
    };
  },
  methods: {
    async createShirt() {
      const variables = {
        color: this.shirtColor,
        size: this.shirtSize,
        creator: this.creator,
      };
      console.log(variables);
      const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
      const query = `
      mutation createShirt($color: String, $size: ShirtSize, $creator: String) {
        createShirt(color: $color, size: $size, creator: $creator) {
          id,
        },
      }
      `;
      const body = {
        query,
        variables,
      };
      const response = (await axios.post(endpoint, body)).data;
      console.log("Response:", response);
      const { id } = response.data.createShirt;
      console.log("Created Shirt ID:", id);
      this.$router.push({ name: "colored", params: { id } });
    },
  },
  components: {
    Shirt,
  },
};
</script>

